import React, { Component } from 'react';
import { Route } from 'react-router';
import pd_png from '../../assets/image/pd.png';
import { Carousel, List, Tabs, Button } from 'antd-mobile';
import CustomWhiteSpace from '../../component/customWhiteSpace';
import TextMaskImageBox from '../../component/textMaskImageBox';
import asyncC from '../../component/asyncC';
import styles from './detail.less';
const Counselor = asyncC(() => import('./counselor'));
const Designer = asyncC(() => import('./designer'));

export default class nearbyShopDetail extends Component {

    state = {
        docT: '',
    }

    jumpTo = ({ pathname, state = {} }) => {
        const { history } = this.props;
        history.push({
            pathname,
            state
        })
    }

    componentDidMount() {
        const { location } = this.props,
            { state = {} } = location,
            { name = '门店详情' } = state;
        this.setState({ docT: name })
        document.title = name;
    }

    componentWillReceiveProps() {
        document.title = this.state.docT;
    }

    render() {
        const { match } = this.props;
        return (
            <div className={styles.wrapper}>
                <div className={styles.wrapper_container}>
                    <Carousel
                        autoplay={false}
                        infinite
                        className='carousel_common'
                    >
                        {[pd_png, pd_png, pd_png].map((val, index) => (
                            <span
                                key={index}
                                className='carousel_common_item'
                            >
                                <img
                                    src={val}
                                    alt=""
                                    onLoad={() => {
                                        // fire window resize event to change height
                                        window.dispatchEvent(new Event('resize'));
                                    }}
                                />
                            </span>
                        ))}
                    </Carousel>
                    <List>
                        <List.Item><div className='titleFontSizeC'>东莞南城金隅香书店</div></List.Item>
                        <List.Item thumb={<i className='iconfont icon-address redColor' />}><div className='normalFontSizeC shallowGreyColor'>雅居乐天小区</div></List.Item>
                        <List.Item thumb={<i className='iconfont icon-phone greenColor' />}><div className='normalFontSizeC shallowGreyColor'>13548756656</div></List.Item>
                        <List.Item thumb={<i className='iconfont icon-code redColor' />}><div className='normalFontSizeC shallowGreyColor'>店铺二维码（访问量：3200）</div></List.Item>
                    </List>
                    <CustomWhiteSpace />
                    <div className={styles.wrapper_container_personal}>
                        <div className={`${styles.wrapper_container_personal_header} titleFontSizeC`}>设计达人</div>
                        <div className={styles.wrapper_container_personal_body}>
                            {[
                                { imgUrl: pd_png, title: '李金丽' },
                                { imgUrl: pd_png, title: '李金丽' },
                                { imgUrl: pd_png, title: '李金丽' }].map((item, index) => <TextMaskImageBox
                                    key={index}
                                    style={{ margin: index % 3 === 1 ? '0 2%' : null }}
                                    className={styles.wrapper_container_personal_body_item}
                                    rowData={{ imgUrl: item.imgUrl, title: item.title }}
                                    onClick={this.jumpTo.bind(this, { pathname: match.path + '/designer', state: item })}
                                />)
                            }
                        </div>
                    </div>
                    <CustomWhiteSpace />
                    <div className={styles.wrapper_container_personal}>
                        <div className={`${styles.wrapper_container_personal_header} titleFontSizeC`}>资深家居顾问</div>
                        <div className={styles.wrapper_container_personal_body}>
                            {[
                                { imgUrl: pd_png, title: '李金丽' },
                                { imgUrl: pd_png, title: '李金丽' }
                            ].map((item, index) => <TextMaskImageBox
                                key={index}
                                style={{ margin: index % 3 === 1 ? '0 2%' : null }}
                                className={styles.wrapper_container_personal_body_item}
                                rowData={{ imgUrl: item.imgUrl, title: item.title }}
                                onClick={this.jumpTo.bind(this, { pathname: match.path + '/counselor', state: item })}
                            />)
                            }
                        </div>
                    </div>
                    <CustomWhiteSpace />
                    <Tabs
                        tabs={[
                            { title: '精选方案' },
                            { title: '品牌介绍' },
                        ]}
                        tabBarTextStyle={{ color: '#AEAEAE' }}
                        tabBarActiveTextColor='#DA4943'
                        tabBarUnderlineStyle={{ borderColor: '#FF0000' }}
                        prerenderingSiblingsNumber={0}
                    >
                        <div>精选方案</div>
                        <div>品牌介绍</div>
                    </Tabs>
                </div>
                <div className={styles.wrapper_footer}>
                    <Button type='warning'>预约量房</Button>
                </div>
                <Route path={match.path + '/counselor'} component={Counselor} />
                <Route path={match.path + '/designer'} component={Designer} />
            </div>
        );
    }
}